import React, { useState } from 'react';
import Holder from './Holder';
import styled from 'styled-components';
import StudyObject from './StudyObject';

const AboutWrap = styled.div`
  h1 {
    color: green;
  }

  img {
    display: inline-flex;
    width: 100%;
  }
`;

const ImgWrapper = styled.div`
  display: inline-flex;
  width: 50%;
  height: 100vh;
`;

const Options = styled.div`
  position: absolute;
  width: 50%;
  right: 10px;
  top: 20%;
  padding: 2pc;

  ul {
    list-style: none;
  }

  ul li {
    font-size: 4pc;
  }
`;

const Opt = styled.div`
  padding-bottom: 4pc;
  h3 {
    font-size: 3pc;
    color: black;
  }

  p {
    font-size: 1pc;
    color: gray;
  }
`;

const Content = styled.div`
  padding: 20px;
`;

const About = () => {
  const [showHolder, setShowHolder] = useState(false);
  const [showStudyObject, setShowStudyObject] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const [showBtnBack, setShowBtnBack] = useState(false);

  return (
    <AboutWrap>
      {!showContent ? (
        <Options>
          <Opt>
            <h3>Elección de soporte</h3>
            <p>
              Se eligió como soporte un sitio web con el objetivo de incorporar una 
              nueva herramienta de comunicación accesible desde ...
            </p>
            <button type="button" onClick={() => {
              setShowHolder(true);
              setShowStudyObject(false);
              setShowContent(true);
              setShowBtnBack(true);
            }}>Ver mas</button>
          </Opt>
          <Opt>
            <h3>Objeto de estudio</h3>
            <p>
              El objeto de estudio del trabajo son los mercados  
              latinoamericanos desde los primeros casos donde surgieron ...
            </p>
            <button onClick={() => {
              setShowHolder(false);
              setShowStudyObject(true);
              setShowContent(true);
              setShowBtnBack(true);
            }}>Ver mas</button>
          </Opt>
        </Options>
      ) : (
        <Content>
          {showBtnBack ? (
            <button type="button" onClick={() => {
              setShowHolder(false);
              setShowStudyObject(false);
              setShowContent(false);
            }}>Volver</button>
          ) : (
            ''
          )}
          {showHolder ? (
            <Holder />
          ) : (
            ''
          )}
          {showStudyObject ? (
            <StudyObject />
          ) : (
            ''
          )}
        </Content>
      )}

      <ImgWrapper>
        {!showContent ? (
          <img src={`${process.env.PUBLIC_URL}/images/img-2.jpeg`}  alt="img-2" />
        ) : (
          ''
        )}
      </ImgWrapper>
      
    </AboutWrap>
    
  );
}

export default About;